/* Variable definitions */
/* Reusable Styles */
.text-style { font-family: "Open Sans", -apple-system, system-ui, sans-serif; font-size: 16px; font-weight: 500; color: #555; }

.heading-style { font-family: "Open Sans", -apple-system, system-ui, sans-serif; font-style: oblique; font-weight: 600; }

.title-style { font-size: 36px; font-family: "Open Sans", -apple-system, system-ui, sans-serif; font-style: normal; font-weight: 700; color: #333344; }

.card-style, .map-card-style { border: 1px solid #eaeaea; background: #fff; padding: 18px; margin-bottom: 18px; border-radius: 2px; }

.light-text { color: #aaa; }

.button-style, .MapPage .action, .map-button-style { cursor: pointer; user-select: none; border-radius: 2em; color: white; padding: 0.5em 1em; font-size: 16px; font-weight: 600; font-family: "Open Sans", -apple-system, system-ui, sans-serif; border: 1px solid #ccc; display: inline-block; -webkit-font-smoothing: antialiased; margin-bottom: 0.5em; transition: background-color 0.3s, transform 0.3s, border 0.3s; box-shadow: -1.5px -1.5px 0px rgba(0, 0, 0, 0.1) inset; transform: scale(1); }

.button-style:hover, .MapPage .action:hover, .map-button-style:hover { text-decoration: none; transform: scale(1.03); }

.button-style:active, .MapPage .action:active, .map-button-style:active { transform: scale(0.95); }

.button-style.full-width, .MapPage .full-width.action, .full-width.map-button-style { display: block; }

.button-style.with-icon, .MapPage .with-icon.action, .with-icon.map-button-style { position: relative; padding-left: 2.4em; }

.button-style.with-icon:hover .icon, .MapPage .with-icon.action:hover .icon, .with-icon.map-button-style:hover .icon { transform: scale(1.08); }

.button-style.with-icon .icon, .MapPage .with-icon.action .icon, .with-icon.map-button-style .icon { position: absolute; left: 0.3em; top: 0.3em; bottom: 0.3em; width: 1.8em; border-radius: 1em; transition: transform 0.4s; transform: scale(1); background-size: contain; background-repeat: no-repeat; }

.button-style.primary, .MapPage .primary.action, .primary.map-button-style { background: #3385CB; border-color: #2f7abb; }

.button-style.primary:hover, .MapPage .primary.action:hover, .primary.map-button-style:hover { background-color: #2e78b7; }

.button-style.primary .icon, .MapPage .primary.action .icon, .primary.map-button-style .icon { color: #3385CB; }

.button-style.secondary, .MapPage .secondary.action, .secondary.map-button-style { background: #aaaab1; border-color: #9f9fa7; }

.button-style.secondary:hover, .MapPage .secondary.action:hover, .secondary.map-button-style:hover { background-color: #9d9da5; }

.button-style.secondary .icon, .MapPage .secondary.action .icon, .secondary.map-button-style .icon { color: #aaaab1; }

.button-style.green, .MapPage .green.action, .green.map-button-style { background: #5cb85c; border-color: #4eb24e; }

.button-style.green:hover, .MapPage .green.action:hover, .green.map-button-style:hover { background-color: #4cae4c; }

.button-style.green .icon, .MapPage .green.action .icon, .green.map-button-style .icon { color: #5cb85c; }

.button-style.blue, .MapPage .blue.action, .blue.map-button-style { background: #3c87c7; border-color: #357db9; }

.button-style.blue:hover, .MapPage .blue.action:hover, .blue.map-button-style:hover { background-color: #347ab5; }

.button-style.blue .icon, .MapPage .blue.action .icon, .blue.map-button-style .icon { color: #3c87c7; }

.button-style.red, .MapPage .red.action, .red.map-button-style { background: #cb4a4a; border-color: #c63a3a; }

.button-style.red:hover, .MapPage .red.action:hover, .red.map-button-style:hover { background-color: #c33838; }

.button-style.red .icon, .MapPage .red.action .icon, .red.map-button-style .icon { color: #cb4a4a; }

.button-style.orange, .MapPage .orange.action, .orange.map-button-style { background: #e88a46; border-color: #e67f34; }

.button-style.orange:hover, .MapPage .orange.action:hover, .orange.map-button-style:hover { background-color: #e57c2f; }

.button-style.orange .icon, .MapPage .orange.action .icon, .orange.map-button-style .icon { color: #e88a46; }

.button-style.purple, .MapPage .purple.action, .purple.map-button-style { background: #b19cd9; border-color: #a58dd3; }

.button-style.purple:hover, .MapPage .purple.action:hover, .purple.map-button-style:hover { background-color: #a28ad2; }

.button-style.purple .icon, .MapPage .purple.action .icon, .purple.map-button-style .icon { color: #b19cd9; }

html { height: 100%; }

.survey-response label.name { font-size: 18px; font-style: italic; margin: 0px; }

.survey-response p.value { font-weight: 400; }

.MapPage, #map-app, #map-base, .main { width: 100%; height: 100%; position: relative; }

.MapPage { /* header { // ... } .main { // padding: 0px; } a:focus { text-decoration: none; } #map-app { background: #fafafa; position: relative; height: 100%; #map { width: 100%; height: 100%; .leaflet-marker-icon:focus { outline: none; } } &.selecting { #map { cursor: crosshair !important; } .leaflet-interactive { pointer-events: none; } } #map-detail { position: absolute; top: 0; right: 0; width: 500px; z-index: 5000; padding-bottom: 0px; overflow-x: hidden; @extend .map-card-style; .title { position: relative; margin: -$cardPadding; margin-bottom: 0; padding: $cardPadding; padding-right: 82px; .menu { position: absolute; top: 12px; right: 12px; user-select: none; i { cursor: pointer; color: $primaryColour; } } } .inner { padding-bottom: $cardPadding; } } #map-actions { position: absolute; top: 0; right: 0; margin-top: $cardPadding; margin-right: $cardPadding; width: 220px; z-index: 2000; .action { @extend .map-button-style; &:last-child { margin-bottom: 0px; } } } #map-controls { @extend .map-card-style; position: absolute; left: 0; top: 0; width: 320px; z-index: 4000; .title { margin-top: 0px; border-bottom: 1px solid #ccc; padding-bottom: 10px; text-align: center; } } #map-overlay { position: absolute; top: 0; bottom: 0; left: 0; right: 0; background: rgba(0,0,0,0.3); z-index: 1500; padding: 0px $cardPadding; pointer-events: none; .message { @extend .map-card-style; text-align: center; margin-top: $cardPadding; font-size: 18px; color: $darkColour; max-width: 550px; min-width: 100%; margin-left: auto; margin-right: auto; } } #map-cancel-button { position: absolute; bottom: 0; width: 100%; margin: auto; z-index: 3000; text-align: center; margin-bottom: 32px; .button.web { @extend .map-button-style; display: inline-block; margin: 0; } .button.mobile { @extend .mobile-button-style; background-image: url(../images/icons/cancel-button.svg); margin: auto; } } #mobile-buttons { position: absolute; left: 0; bottom: 0; z-index: 3000; margin-left: calc(18px * 0.5); margin-bottom: 32px; p.button { float: left; margin-right: 12px; @extend .mobile-button-style; &.actions { background-image: url(../images/icons/actions-button.svg); } &.controls { background-image: url(../images/icons/controls-button.svg); } } } } */ }

.MapPage .main { padding-bottom: 48px; }

@media only screen and (max-width: 767px) { .MapPage .main { padding-bottom: 0px; } }

.MapPage footer { position: absolute; left: 0; right: 0; bottom: 0; }

.MapPage #map-base { width: 100%; height: 100%; }

.MapPage #map-base .leaflet-marker-icon:focus { outline: none; }

.MapPage .action-list { position: absolute; top: 18px; right: 18px; display: block; z-index: 500; text-align: right; }

.MapPage .action-list .action { font-size: 18px; }

.MapPage .mobile-action-toggle { position: absolute; left: 12px; bottom: 22px; z-index: 500; }

.MapPage .is-mobile .action-list { top: inherit; bottom: 42px; left: 12px; right: 12px; text-align: center; }

[v-cloak] { display: none; }

.map-button-style { box-shadow: 1px 1px 1px #666; font-size: 20px; }

.map-card-style { padding: 12px; border: 2px solid #ccc; margin: 18px; max-width: 100%; overflow: scroll; max-height: calc(100% - 18px - 18px); }

.mobile-button-style { margin: 0px; width: 48px; height: 48px; background-size: contain; background-repeat: no-repeat; }

.fade-enter-active, .fade-leave-active { transition: opacity .2s ease; }

.fade-enter, .fade-leave-to { opacity: 0; }

.grow-fade-enter-active, .grow-fade-leave-active { transition: opacity .2s ease, transform .2s ease; }

.grow-fade-enter, .grow-fade-leave-to { opacity: 0; transform: scale(0.5); }

/* Small Devices: Phones */
@media only screen and (max-width: 767px) { html body.MapPage footer { display: none; }
  html body.MapPage #map-app #map-detail .survey-response label.name { font-size: 14px; }
  html body.MapPage #map-app #map-detail .survey-response p.value { font-size: 12px; }
  html body.MapPage #map-app #map-controls, html body.MapPage #map-app #map-detail { right: 0; left: 0; margin: calc(18px * 0.5); width: inherit; }
  html body.MapPage #map-app #map-controls .title, html body.MapPage #map-app #map-detail .title { font-size: 28px; }
  html body.MapPage #map-app #map-controls { top: inherit; bottom: calc(48px + 18px); margin-bottom: calc(18px * 2.5); }
  html body.MapPage #map-app #map-actions { top: inherit; right: 0; left: 0; bottom: 48px; margin: 18px auto; margin-bottom: calc(18px * 2.5); }
  html body.MapPage #map-app #map-actions p { font-size: 24px; } }



